<template>
  <!-- 组件列表展示 -->
  <div class="content">
    <!-- 按钮 -->
    <div id="Button" class="layout">
      <p class="tit">按钮（Button）</p>
      <div class="base_box">
       <Button
          :butContent="'信息'"
          :type="'info'"
          :float="'l'"
          @click="butfn"
        />
        <Button
          :butContent="'成功'"
          :type="'success'"
          :float="'l'"
          @click="butfn"
        />
        <Button
          :butContent="'警告'"
          :type="'warning'"
          :float="'l'"
          @click="butfn"
        />
        <Button
          :butContent="'默认'"
          :type="'default'"
          :float="'l'"
          @click="butfn"
        />
        <Button
          :butContent="'错误'"
          :type="'error'"
          :float="'l'"
          @click="butfn"
        />
      </div>
    </div>
    <!-- 确认提示框-->
    <div id="Confirm" class="layout">
      <p class="tit">确认提示框（$confirm）</p>
      <div class="base_box">
       <Button
          :butContent="'确认提示框'"
          @click="ConfirmFn()"
        />
      </div>
    </div>
    <!-- 信息提示框 -->
    <div id="Tips" class="layout">
      <p class="tit">信息提示框（$tips）</p>
      <div class="base_box">
       <Button
          :butContent="'信息提示'"
          :float="'l'"
          :type="'success'"
          :Width="'40%'"
          @click="tipsFn()"
        />
        <Button
          :butContent="'标题信息提示'"
          :float="'r'"
          :type="'success'"
          :Width="'40%'"
          @click="tipsFn1()"
        />
      </div>
    </div>
     <!-- 滚动条盒子 -->
    <div id="scrollContainer" class="layout">
      <p class="tit">自定义滚动条（scrollContainer）</p>
      <div class="layout_box">
        <scrollContainer
          ref="scrollbarRef"
          height="100%"
          class="scroll-container"
        >
          <ul v-for="item in 17" :key="item">
            <li>{{ item }} 信息列表</li>
          </ul>
        </scrollContainer>
      </div>
       <a
          class="my_a"
          target="_blank"
          href="https://blog.csdn.net/weixin_42681555/article/details/121628931?spm=1001.2014.3001.5502"
          >查看代码
        </a>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {
    // *注：demo更新需要更新容器滚动条
    this.$nextTick(() => {
      this.$refs.scrollbarRef.updateScroll();
    });
  },
  methods: {
    // 按钮触发事件
    butfn(type){
      // 自定义弹框
      this.$tips({
        type:type,
        msg: type+ " 类型",
        top:30,
        time:1000
      })
    },
    // 确认提示框调用方式
    ConfirmFn() {
      this.$confirm({
        type: "提示标题",
        msg: "提示内容自定义，全局使用",
        btn: {
          ok: "确定",
          no: "取消",
        },
      }) .then(() => {
         // 自定义弹框
          this.$tips({
            type:'success',
            msg:"确认",
            top:30,
            time:1000
          })
      }).catch(() => {
          // 自定义弹框
          this.$tips({
            type:'info',
            msg: " 取消",
            top:30,
            time:1000
          })
      });

      
    },
    // 信息提示框按键展示
    tipsFn(){
      // 自定义弹框
      this.$tips({
        type:'success',
        msg: '提示信息文字',
        top:30,
        time:1000
      })
    },
    // 标题信息提示框按键展示
    tipsFn1(){
      // 自定义弹框
      this.$tips({
        type:'success',
        tit:'提示标题',
        msg: '提示信息文字',
        top:30,
        time:1000
      })
    }
  },
};
</script>

<style lang="less" scoped>
.layout {
  padding: 20px;
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
  box-sizing: border-box;
  overflow: hidden;
  .tit {
    margin: 10px 0;
    box-sizing: border-box;
    font-size: 20px;
    font-weight: 700;
  }
  .my_a {
    text-decoration: none;
    color: rgb(133, 0, 173);
    line-height: 40px;
    font-size: 16px;
    float: right;
  }
}
// 滚动条盒子
.layout_box {
  border: 1px solid #ddd;
  margin: 0 auto;
  height: 200px;
  overflow: hidden;
}
// 基础盒子
.base_box{
  overflow: hidden;
}

</style>
